@import url(https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,400;0,500;0,600;1,300;1,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Zilla+Slab:ital@0;1&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.DeparElemForm{

    display: flex;
    flex-direction: column;
    padding-top: 100px;
    padding-left: 58px;
    width: 40%;


}
.input{
    margin-top: 50px;

}


.DeparElemForm h2{

    margin-bottom: 20px;
    color: #09282F;
}



.container_depart{
    width: 330px;
    background-repeat: no-repeat;
    margin-right: 30px;
    padding-left: 37px;
    padding-right: 37px;
    box-sizing: border-box;
}

.container_depart--title{
    padding-top: 99%;
    color: white;
    font-family: Jost;
    font-style: normal;
    font-weight: 600;
    box-sizing: border-box;


}

.container_depart--des{
    color: white;
    font-family: Jost;
    font-style: italic;
}

.link{
    color: white;
    padding-right: 40px;
    text-decoration: none;
    font-family: 'DM Sans', sans-serif;
    font-size: 15px;
}



.link--active{

    color: #FFE766;
    font-weight: bold;


    
}
